<template>
  <div id="login">
    <div class="loginConbox">
      <div class="header">
        <!--<div class="logo">-->
        <!--<img src="../../assets/logo.png">-->
        <!--</div>-->
      </div>
      <div class="loginBox">
        <div class="loginCon">
          <p class="title">{{ $t('login.disasterRecovery') }}</p>
          <p class="title">{{ $t('login.frontEnd') }}: vue + element-ui</p>
          <p class="title">{{ $t('login.backEnd') }}: Springboot</p>
          <el-card shadow="always"
                   class="login-module"
                   v-if="smdl">
            <div slot="header"
                 class="clearfix formTitlt">
              <span>{{ $t(`login.signIn`) }}</span>
              <span class="titIconbox">
                <i class="iconfont xu-saomadenglu2 fa-lg iconcolor"></i>
                <i class="iconfont xu-saomadenglu01 el-icon--right fa-lg pointer"
                   @click="smdl = !smdl"></i>
              </span>
            </div>
            <el-form :model="loginForm"
                     :rules="rules"
                     status-icon
                     label-width="100px"
                     ref="loginForm"
                     class="demo-ruleForm"
                     size="small">
              <el-form-item prop="username">
                <el-input type="text"
                          v-model="loginForm.username"
                          auto-complete="off"
                          placeholder="Please input user name."></el-input>
              </el-form-item>
              <el-form-item prop="password">
                <el-input type="password"
                          v-model="loginForm.password"
                          auto-complete="off"
                          placeholder="please input password"
                          show-password></el-input>
              </el-form-item>
              <el-form-item>
                <el-select v-model="loginForm.language"
                           placeholder=""
                           style="width:100%"
                           @chang="langChange">
                  <el-option v-for="item in langData"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button class="subBtn"
                           type="primary"
                           @click="submitForm">{{ $t(`login.signIn`) }}</el-button>
              </el-form-item>
              <p class="smalltxt">
                <span style="font-size: 13px;">{{ $t(`login.forgotPassword`) }}</span>
                <!-- <router-link class="a" to="#">忘记密码</router-link>
                <router-link class="a" to="#">忘记会员名</router-link>
                <router-link class="a" to="#">免费注册</router-link> -->
              </p>
            </el-form>
          </el-card>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      smdl: true,
      loginForm: {
        username: '',
        password: '',
        language: 'en'
      },
      language: '',
      langData: [
        { label: '中文', value: 'cn' },
        { label: 'English', value: 'en' }
      ],
      rules: {
        username: [
          { required: true, message: 'Please input user name.', trigger: 'blur' },
          { min: 4, max: 10, message: 'length between 4 and 10 characters.', trigger: 'blur' }
        ],
        password: [
          { required: true, message: 'Please input password.', trigger: 'blur' },
          { min: 4, max: 16, message: 'length between 4 and 16 characters.', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    langChange (val) {
      this.$i18n.locale = val
      localStorage.setItem('lang', val)
    },
    submitForm () {
      this.$refs.loginForm.validate((valid) => {
        if (!valid) {
          return false
        } else {
          let that = this
          // 将 username 设置为 token 存储在 store，仅为测试效果，实际存储 token 以后台返回为准
          that.$store.dispatch('setToken', that.loginForm.username).then(() => {
            that.$router.push({ path: '/index' })
          }).catch(res => {
            that.$message({
              showClose: true,
              message: res,
              type: 'error'
            })
          })
        }
      })
    },
    message () {
      const h = this.$createElement
      this.$notify({
        title: '账号密码',
        message: h('i', { style: 'color: teal' }, '账号密码暂时可以随意填写'),
        duration: 3000
      })
    }
  },
  mounted () {
    this.message()
  },
  created () {
    this.langChange(this.loginForm.language)
  }
}
</script>
<style lang="scss">
#login {
  width: 100%;
  height: 97vh;
  margin: 0;
  padding: 0;
  // background-color: #2d3a4b;
  background: url(../../assets/login/blackground.png) no-repeat;
  background-size: 100% 100%;
  .loginConbox {
    //   background: #2d3a4b;
  }
  .header {
    height: 60px;
    position: relative;
    //   background: #2d3a4b;
    /*border-bottom: 1px solid rgba(255, 255, 255, 0.3);*/
  }

  .loginBox {
    .iconcolor {
      color: #409eff;
    }

    padding: 74px 0 118px;

    .loginCon {
      width: 990px;
      margin: auto;
      position: relative;
      height: 388px;

      .el-card__header {
        border-bottom: 0px;
      }
      .title {
        font-size: 36px;
        font-weight: 600;
        color: #ffffff;
        width: 500px;
        float: left;
        margin-top: 0px;
        &:first-child {
          font-size: 34px;
          margin-top: 50px;
          margin-bottom: 30px;
        }
      }
      .login-module {
        width: 380px;
        height: 325px;
        margin-top: 60px;
        border: none;
        position: absolute;
        right: 0;

        .formTitlt {
          font-size: 18px;
          font-weight: 400;

          .titIconbox {
            float: right;

            .pointer {
              cursor: pointer;
            }
          }
        }

        .smalltxt {
          text-align: right;

          .a {
            text-decoration: none;
            color: #999999;
            font-size: 12px;
            margin-left: 8px;
          }
        }
      }

      .el-form-item__content {
        margin-left: 0px !important;

        .subBtn {
          width: 100%;
        }
      }
    }

    .el-input__inner,
    .el-button,
    .el-card,
    .el-message {
      border-radius: 0px !important;
    }

    .el-form-item__content .ico {
      position: absolute;
      top: 0px;
      left: 0px;
      z-index: 999;
      width: 40px;
      height: 39px;
      text-align: center;
      border-right: 1px solid #ccc;
    }

    .ewmbox {
      width: 100%;
      height: 240px;
      margin-top: -25px;

      .ewm {
        width: 140px;
        height: 140px;
        margin: 20px auto;

        p {
          font-size: 12px;
          padding-left: 40px;
          margin: 0;
        }
      }

      .ewmicon {
        width: 140px;
        margin: 20px auto 0;

        .iconfont {
          float: left;
        }

        p {
          font-size: 12px;
          padding-left: 40px;
          margin: 0;
        }
      }
    }
  }
}
</style>



