<template>
  <el-row :gutter="20">
    <el-col :span="24"
            style="padding-bottom:10px">
      <el-divider content-position="left">Project Config</el-divider>
    </el-col>
    <el-col :span="24">
      <el-table ref="projectTable"
                :data="projectList"
                stripe
                border
                style="width: 100%"
                size="mini"
                :max-height="resultHeight"
                highlight-current-row
                @current-change="handleCurrentChange">
        <!-- <el-table-column fixed prop="projectId" label="ID" width></el-table-column> -->
        <el-table-column prop="name"
                         label="Name"
                         width></el-table-column>
        <el-table-column prop="comments"
                         label="Remarks"
                         width></el-table-column>
        <el-table-column fixed="right"
                         label="Action"
                         width="120">
          <template slot-scope="scope">
            <el-button @click.native.prevent="deleteRow(scope.$index, scope.row)"
                       type="text"
                       size="small">Delete</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
    <el-col :span="24">
      <el-divider></el-divider>
    </el-col>
    <el-col :span="24">
      <el-collapse v-model="activeNames"
                   @change="handleChange">
        <el-collapse-item name="1">
          <template slot="title">
            Detail
          </template>
          <el-col :span="24">
            <el-form ref="form"
                     :model="formData"
                     :rules="rules"
                     size="mini"
                     label-width="auto"
                     :disabled="!editFlag"
                     hide-required-asterisk>
              <el-col :span="12"
                      style="display:none;">
                <el-form-item label="Project Id"
                              prop="id">
                  <el-input v-model="formData.projectId"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="Project Name"
                              prop="name"
                              required>
                  <el-input v-model="formData.name"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="Comments"
                              prop="comments">
                  <el-input v-model="formData.comments"></el-input>
                </el-form-item>
              </el-col>
            </el-form>
          </el-col>
          <el-col :span="24"
                  v-show="!editFlag"
                  class="js-btn-group">
            <el-button type="primary"
                       @click="onAdd"
                       :disabled="!addPermit"
                       size="mini">{{ $t(`new`) }}</el-button>
            <el-button type="primary"
                       @click="onEdit"
                       :disabled="!editPermit"
                       size="mini">{{ $t(`edit`) }}</el-button>
            <!-- <el-button size="mini">{{ $t(`delete`) }}</el-button> -->
          </el-col>
          <el-col :span="24"
                  v-show="editFlag"
                  class="js-btn-group">
            <el-button type="primary"
                       @click="onSubmit"
                       size="mini">{{ $t(`el.colorpicker.confirm`) }}</el-button>
            <el-button @click="onCancel"
                       size="mini">{{ $t(`el.messagebox.cancel`) }}</el-button>
          </el-col>
        </el-collapse-item>
      </el-collapse>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: 'projectTable',
  components: {},
  created () {
    this.reloadProjectTable()
  },
  data () {
    return {
      resultHeight: this.$store.state.clientHeight - 380,
      addPermit: true,
      editPermit: true,
      editFlag: false,
      action: 'view',
      activeNames: ['1'],
      projectList: [],
      // form: {},
      formData: {
        projectId: '',
        name: '',
        comments: ''
      },
      currentRow: {
        projectId: '',
        name: '',
        comments: ''
      },
      rules: {
        // id: [
        //   { required: this.action === 'edit', message: 'Please input id.', trigger: 'blur' },
        //   { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        // ],
        name: [
          { required: true, message: 'Please input project name.', trigger: 'blur' },
          { min: 3, max: 100, message: 'project name length should between 3 and 100.', trigger: 'blur' }
        ],
        comments: [
          { min: 0, max: 1024, message: 'Comments length should less than 1024.', trigger: 'blur' }
        ]
      },
      formDisabled: true
    }
  },
  methods: {
    reloadProjectTable () {
      this.axios.get('/project').then((response) => {
        console.log(response.data)
        this.projectList = response.data.item || []
        if (this.projectList.length === 0) {
          this.formData = []
          this.editPermit = false
          return
        }
        this.$refs.projectTable.setCurrentRow(this.projectList[0])
      }).catch((response) => {
        console.log(response)
      })
    },
    deleteRow (index, row) {
      this.$confirm('Are you sure to delete ?', 'Confirm', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        this.axios.delete(`/project/${row.projectId}`).then((response) => {
          console.log(response.data)
          this.$message({
            type: 'success',
            message: 'Delete project success!'
          })
          this.projectList.splice(index, 1)
          if (this.projectList.length === 0) {
            this.editPermit = false
          }
          this.reloadProjectTable()
        }).catch((response) => {
          console.log(response)
        })
      }).catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: 'Delete canceled.'
        // })
      })
    },
    handleChange (val) {
      console.log(val)
    },
    handleCurrentChange (val) {
      this.action = 'view'
      this.editPermit = true
      this.editFlag = false
      this.currentRow = val
      this.formData = JSON.parse(JSON.stringify(val))
    },
    onAdd () {
      this.editFlag = true
      this.action = 'new'
      this.$refs.form.resetFields()
      this.formData.projectId = ''
      this.formDisabled = false
    },
    onEdit () {
      this.editFlag = true
      this.action = 'edit'
      this.formDisabled = false
      console.log('eidt')
    },
    onSubmit () {
      this.$refs['form'].validate((valid) => {
        if (!valid) {
          console.log('error submit!!')
          return false
        }
        if (this.action === 'new') {
          this.axios.post(`/project`, this.formData).then((response) => {
            console.log(response.data)
            if (response.data.resultCode === 0) {
              this.$message({
                message: 'Add project success.',
                type: 'success'
              })
              this.projectList.push(response.data.entity)
              this.reloadProjectTable()
              return
            }
            this.$message({
              message: response.data.errMsg,
              type: 'warning'
            })
          }).catch((response) => {
            console.log(response)
          })
        } else if (this.action === 'edit') {
          this.axios.put(`/project`, this.formData).then((response) => {
            console.log(response.data)
            if (response.data.resultCode === 0) {
              this.$message({
                message: 'Modify project success.',
                type: 'success'
              })
              this.reloadProjectTable()
              return
            }
            this.$message({
              message: response.data.errMsg,
              type: 'warning'
            })
          }).catch((response) => {
            console.log(response)
          })
        }
      })
    },
    onCancel () {
      this.editFlag = false
      this.action = 'view'
      this.formDisabled = true
      console.log('on cancel')
      this.formData = JSON.parse(JSON.stringify(this.currentRow))
    }
  }
}
</script>

<style scoped>
.el-collapse-item__header {
  padding-left: 10px;
  color: cornflowerblue;
  font-weight: 600;
}
.el-divider--horizontal {
  margin: 12px 0;
}
h3 {
  margin: 25px 0 20px;
  font-weight: 400;
  color: #1f2f3d;
  font-size: 22px;
}
p {
  font-size: 14px;
  color: #5e6d82;
  line-height: 1.5em;
}
.el-divider__text {
  position: absolute;
  background-color: #f0f2f5;
  padding: 0 20px;
  color: #303133;
}
.js-btn-group {
  text-align: right;
  padding-bottom: 15px;
}
</style>



