<template>
  <el-row :gutter="20">
    <el-col :span="24"
            style="padding-bottom:10px">
      <el-divider content-position="left">Server Config</el-divider>
    </el-col>
    <el-col :span="12"
            style="height: 50px;">
      <projectSelect @projectChange="projectChange"></projectSelect>
    </el-col>
    <el-col :span="24">
      <el-table ref="serverTable"
                :data="serverData"
                max-height="400"
                size="mini"
                highlight-current-row
                @current-change="handleCurrentChange">
        <el-table-column prop="hostIp"
                         label="HostIp"></el-table-column>
        <el-table-column prop="port"
                         label="port"></el-table-column>
        <el-table-column prop="user"
                         label="user"></el-table-column>
        <el-table-column prop="password"
                         label="password"
                         :formatter="formatPassword"></el-table-column>
        <el-table-column prop="homeDir"
                         label="Home Directory"></el-table-column>
        <el-table-column prop="masterFlag"
                         label="Is Master"
                         :formatter="formatMasterFlag"></el-table-column>
        <el-table-column fixed="right"
                         label="Action"
                         width="120">
          <template slot-scope="scope">
            <el-button @click.native.prevent="deleteRow(scope.$index, scope.row)"
                       type="text"
                       size="mini">{{ $t("delete") }}</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
    <el-col :span="24">
      <el-divider></el-divider>
    </el-col>
    <el-col :span="24">
      <el-collapse v-model="activeNames"
                   @change="handleChange">
        <el-collapse-item name="1">
          <template slot="title">
            Detail
          </template>
          <el-col :span="16">
            <el-col :span="24">
              <el-form ref="serverForm"
                       :model="formData"
                       label-width="auto"
                       :disabled="!editFlag"
                       size="mini"
                       :rules="rules">
                <el-col :span="12"
                        style="display:none;">
                  <el-form-item label="Project Id"
                                prop="projectId">
                    <el-input v-model="projectId"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12"
                        style="display:none;">
                  <el-form-item label="Host Id"
                                prop="hostId">
                    <el-input v-model="formData.hostId"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="Host IP"
                                prop="hostIp"
                                :disabled="editFlag && ipEditFlag"
                                required>
                    <el-input v-model="formData.hostIp"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="Port"
                                prop="port">
                    <el-input v-model="formData.port"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="user"
                                prop="user">
                    <el-input v-model="formData.user"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="password"
                                prop="password">
                    <el-input v-model="formData.password"
                              show-password></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="Home Directory"
                                prop="homeDir">
                    <el-input v-model="formData.homeDir"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="Is Master"
                                prop="masterFlag">
                    <el-select v-model="formData.masterFlag"
                               placeholder=""
                               style="width:100%">
                      <el-option v-for="item in masterData"
                                 :key="item.value"
                                 :label="item.label"
                                 :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-form>
            </el-col>
          </el-col>
          <el-col :span="8">
            <el-col :span="24">
              <el-table ref="valueTable"
                        :data="formData.hostDetailList"
                        style="width: 100%"
                        size="mini"
                        max-height="200"
                        highlight-current-row
                        border>
                <el-table-column prop="key"
                                 label="key">
                  <template slot-scope="{row}">
                    <span v-if="editFlag">
                      <el-input size="mini"
                                placeholder=""
                                v-model="row.key">
                      </el-input>
                    </span>
                    <span v-else>{{ row.key }}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="value"
                                 label="value">
                  <template slot-scope="{row}">
                    <span v-if="editFlag">
                      <el-input size="mini"
                                placeholder="Please input the value."
                                v-model="row.value">
                      </el-input>
                    </span>
                    <span v-else>{{ row.value }}</span>
                  </template>
                </el-table-column>
                <el-table-column label="Action"
                                 width="60"
                                 align="center">
                  <template slot-scope="scope">
                    <el-button @click.native.prevent="deleteValueRow(scope.$index, formData.hostDetailList)"
                               :disabled="!editFlag"
                               type="text"
                               size="small"
                               icon="el-icon-close"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-col>
            <el-col :span="24"
                    style="text-align: left;padding-top: 10px">
              <el-button type="primary"
                         @click="onAddAdditionValue"
                         :disabled="!editFlag"
                         size="mini">Add</el-button>
            </el-col>
          </el-col>
          <el-col :span="24"
                  v-show="!editFlag"
                  class="js-btn-group">
            <el-button type="primary"
                       @click="onAdd"
                       :disabled="!addPermit"
                       size="mini">{{ $t(`new`) }}</el-button>
            <el-button type="primary"
                       @click="onEdit"
                       :disabled="!editPermit"
                       size="mini">{{ $t(`edit`) }}</el-button>
            <!-- <el-button size="mini">{{ $t(`delete`) }}</el-button> -->
          </el-col>
          <el-col :span="24"
                  v-show="editFlag"
                  class="js-btn-group">
            <el-button type="primary"
                       @click="onSubmit"
                       size="mini">{{ $t(`el.colorpicker.confirm`) }}</el-button>
            <el-button @click="onCancel"
                       size="mini">{{ $t(`el.messagebox.cancel`) }}</el-button>
          </el-col>
        </el-collapse-item>
      </el-collapse>
    </el-col>
  </el-row>
</template>

<script>
import projectSelect from './projectSelect'
export default {
  name: 'serverTable',
  components: { projectSelect },
  created () {

  },
  data () {
    var checkIp = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('Host Ip cannot be empty.'))
      }
      let regex = /((2(5[0-5]|[0-4]\d))|[0-1]?\d{1,2})(\.((2(5[0-5]|[0-4]\d))|[0-1]?\d{1,2})){3}/
      if (!regex.test(value)) {
        callback(new Error('Invalid ip address.'))
      } else {
        callback()
      }
    }
    var checkPort = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('Port cannot be null.'))
      }
      let regex = /^\d{1,5}$/
      if (!regex.test(value)) {
        return callback(new Error('Please input a integer between range(1~65535).'))
      }
      value = Number(value)
      if (value < 0 || value > 65535) {
        callback(new Error('port should in range(1~65535).'))
      } else {
        callback()
      }
    }
    return {
      ipEditFlag: false,
      masterData: [
        { label: 'Yes', value: 1 },
        { label: 'No', value: 0 }
      ],
      valueFlag: false,
      addPermit: true,
      editPermit: true,
      editFlag: false,
      action: 'view',
      activeNames: ['1'],
      // valueData: [],
      projectData: [],
      serverData: [],
      currentRow: {},
      formData: {
        hostDetailList: [],
        hostId: ''
      },
      projectId: '',
      baseProperties: [
        'hostIp', 'port', 'user', 'password', 'masterFlag', 'hostId', 'homeDir'
      ],
      rules: {
        hostIp: [
          { validator: checkIp, trigger: 'blur' }
        ],
        port: [
          { validator: checkPort, trigger: 'blur' }
        ],
        user: [
          { required: true, message: 'This item is required.', trigger: 'blur' }
        ],
        password: [
          { required: true, message: 'This item is required.', trigger: 'blur' }
        ],
        masterFlag: [
          { required: true, message: 'This item is required.', trigger: 'blur' }
        ],
        homeDir: [
          { required: true, message: 'This item is required.', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleClose (done) {
      this.$confirm('确定要提交表单吗？')
        .then(_ => {
          this.loading = true
          setTimeout(() => {
            this.loading = false
            done()
          }, 2000)
        })
        .catch(_ => { })
    },
    projectChange (val) {
      console.log(val)
      this.projectId = val
      this.currentRow = {}
      this.formData = {
        hostDetailList: []
      }
      this.loadServerData(val)
    },
    loadServerData (id) {
      console.log(id)
      this.editFlag = false
      this.ipEditFlag = false
      this.action = 'view'
      this.axios.get(`/host/project/${id}`).then((response) => {
        this.serverData = response.data.item || []
        if (this.serverData.length === 0) {
          this.formData = {
            projectId: '',
            hostId: '',
            hostDetailList: []
          }
          this.$refs.serverForm.resetFields()
          // this.valueData = []
          this.editPermit = false
          return
        }
        this.$refs.serverTable.setCurrentRow(this.serverData[0])
      }).catch((response) => {
        console.log(response)
      })
    },
    formatMasterFlag (row, column, cellValue, index) {
      return cellValue === 1 ? 'Yes' : 'No'
    },
    formatPassword (row, column, cellValue, index) {
      return '**********'
    },
    deleteRow (index, row) {
      this.$confirm('Are you sure to delete ?', 'Confirm', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        this.axios.delete(`/host/${row.hostId}`).then((response) => {
          console.log(response.data)
          this.$message({
            type: 'success',
            message: 'Delete server success!'
          })
          // this.serverData.splice(index, 1)
          this.loadServerData(this.projectId)
        }).catch((response) => {
          console.log(response)
        })
      }).catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: 'Delete canceled.'
        // })
      })
    },
    deleteValueRow (index, rows) {
      rows.splice(index, 1)
    },
    showAdditionValue (index, rows) {
      this.valueFlag = true
    },
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },
    handleChange (val) {
      console.log(val)
    },
    handleCurrentChange (val) {
      this.$refs.serverForm.resetFields()
      this.editPermit = true
      this.currentRow = val
      this.formData = JSON.parse(JSON.stringify(val))
      this.action = 'view'
      this.editFlag = false
      if (this.formData && this.formData.hostDetailList) {
        // this.valueData = this.formData.hostDetailList || []
      } else {
        // this.valueData = []
      }
    },
    onAdd () {
      this.action = 'new'
      this.ipEditFlag = true
      this.editFlag = true
      this.$refs.serverForm.resetFields()
      this.formData = {
        hostId: '',
        projectId: this.projectId,
        hostDetailList: []
      }
      // this.valueData = []
    },
    onEdit () {
      this.ipEditFlag = true
      this.action = 'edit'
      this.editFlag = true
    },
    onAddAdditionValue () {
      if (!this.formData.hostDetailList) {
        this.formData.hostDetailList = []
      }
      this.formData.hostDetailList.push({})
      console.log('add addtion value')
    },
    validateForm (data) {
      if (data.length === 0) {
        return true
      }
      var baseProperties = this.baseProperties
      var that = this
      var flag = true
      data.forEach(function (item, index) {
        if (!item.key || !item.value) {
          that.$message({
            message: 'Please check the addtion value, the key and value is required.',
            type: 'warning'
          })
          flag = false
        }
        if (baseProperties.indexOf(item.key) !== -1) {
          that.$message({
            message: 'The addition value key name cannot be same with the base properties [hostIp, port, user, password, masterFlag, homeDir].',
            type: 'warning'
          })
          flag = false
        }
      })
      return flag
    },
    onSubmit () {
      var addValue = this.formData.hostDetailList || []
      if (!this.validateForm(addValue)) {
        console.log('invalid value')
        return
      }
      // this.formData.hostDetailList = this.valueData
      this.$refs['serverForm'].validate((valid) => {
        if (!valid) {
          console.log('error submit!!')
          return false
        }
        console.log('submit')
        if (this.action === 'new') {
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          })
          this.axios.post(`/host`, this.formData).then((response) => {
            loading.close()
            if (response.data.resultCode === 0) {
              this.$message({
                message: 'Add server success.',
                type: 'success'
              })
              this.serverData.push(response.data.hostInfo)
              this.editFlag = false
              this.loadServerData(this.projectId)
              return
            }
            this.$message({
              message: response.data.errMsg,
              type: 'warning'
            })
          }).catch((response) => {
            loading.close()
            console.log(response)
          })
        } else if (this.action === 'edit') {
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          })
          this.axios.put(`/host`, this.formData).then((response) => {
            loading.close()
            if (response.data.resultCode === 0) {
              this.$message({
                message: 'Modify server success.',
                type: 'success'
              })
              this.loadServerData(this.projectId)
              return
            }
            this.$message({
              message: response.data.errMsg,
              type: 'warning'
            })
          }).catch((response) => {
            loading.close()
            console.log(response)
          })
        }
      })
    },
    onCancel () {
      this.action = 'view'
      this.$refs.serverForm.resetFields()
      this.editFlag = false
      this.ipEditFlag = false
      this.formData = JSON.parse(JSON.stringify(this.currentRow || this.serverData[0] || {}))
    }
  }
}
</script>

<style scoped>
.el-collapse-item__header {
  padding-left: 10px;
  color: cornflowerblue;
  font-weight: 600;
}
.el-divider--horizontal {
  margin: 12px 0;
}
h3 {
  margin: 25px 0 20px;
  font-weight: 400;
  color: #1f2f3d;
  font-size: 22px;
}
p {
  font-size: 14px;
  color: #5e6d82;
  line-height: 1.5em;
}
.el-divider__text {
  position: absolute;
  background-color: #f0f2f5;
  padding: 0 20px;
  color: #303133;
}
.js-btn-group {
  text-align: right;
  /* padding-top: 10px; */
  padding-bottom: 15px;
}
</style>



