<template>
  <el-form>
    <el-form-item label="Project">
      <el-select v-model="value" placeholder="Please select project" @change="projectChange" size="mini">
        <el-option
          v-for="item in projectData"
          :key="item.projectId"
          :label="item.name"
          :value="item.projectId"
        ></el-option>
      </el-select>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'projectSelect',
  created () {
    this.getProjectData()
  },
  data () {
    return {
      project: {},
      projectData: [],
      value: ''
    }
  },
  methods: {
    projectChange (val) {
      this.$emit('projectChange', val)
    },
    getProjectData () {
      this.axios.get('/project').then((response) => {
        this.projectData = response.data.item || []
        if (this.projectData.length === 0) {
        // this.formData = []
          return
        }
        this.value = this.projectData[0].projectId
        this.projectChange(this.projectData[0].projectId)
      }).catch((response) => {
        console.log(response)
      })
    }
  }
}
</script>

<style scoped>
</style>



