import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login/index'
import Layout from '@/views/layout/layout'
// 导入Aboiut组件
import ProjectTable from '@/views/config/project'
import ServerTable from '@/views/config/server'
import FileTable from '@/views/config/file'
import HomeMain from '@/views/index/mainIndex'
import CompareView from '@/views/compare/compare'

const NotFound = () => import('@/page404')

Vue.use(Router)
const originalPush = Router.prototype.push
Router.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}
export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index',
      hidden: true,
      children: []
    },
    {
      path: '/404',
      component: NotFound,
      name: '404',
      hidden: true,
      children: []
    },
    {
      path: '/index',
      iconCls: 'fa fa-dashboard', // 图标样式class
      name: 'Main',
      component: Layout,
      alone: true,
      children: [
        {
          path: '/index',
          iconCls: 'fa fa-dashboard', // 图标样式class
          name: 'Home',
          component: HomeMain,
          children: []
        }
      ]
    },
    {
      path: '/',
      name: 'layout',
      component: Layout,
      children: [
        {
          path: '/project',
          name: 'project',
          component: ProjectTable
        },
        {
          path: '/server',
          name: 'server',
          component: ServerTable
        },
        {
          path: '/file',
          name: 'file',
          component: FileTable
        }
      ]
    },
    {
      path: '/compare',
      iconCls: 'fa fa-dashboard', // 图标样式class
      name: 'Main',
      component: Layout,
      alone: true,
      children: [
        {
          path: '/compare',
          iconCls: 'fa fa-dashboard', // 图标样式class
          name: 'compare',
          component: CompareView,
          children: []
        }
      ]
    },
    {
      path: '/login',
      component: Login,
      name: '',
      hidden: true,
      children: []
    }
  ]
})



