<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="7">
        <el-col :span="24"
                style="padding-bottom:10px;">
          <el-button size="mini"
                     type="primary"
                     @click="collectFile">Collect</el-button>
          <el-button size="mini"
                     type="primary"
                     @click="compareFile">Compare</el-button>
          <el-button size="mini"
                     @click="syncFile1">Sync</el-button>
          <el-button size="mini"
                     @click="downloadReport"
                     icon="el-icon-download">Report</el-button>
        </el-col>
        <el-col :span="24">
          <el-scrollbar :style="treeStyle">
            <el-tree :data="treeData"
                     :props="defaultProps"
                     @node-click="handleNodeClick"
                     node-key="id"
                     default-expand-all
                     :load="loadNode"
                     lazy
                     show-checkbox
                     ref="tree">
              <span class="custom-tree-node"
                    slot-scope="{ node, data }">
                <span :title="data.remotePath">
                  {{ node.label }}
                </span>
                <span>
                  <el-button type="text"
                             size="mini"
                             v-if="node.level === 3 ? true : false"
                             @click.stop="() => compareSingleFile(node, data)">
                    Compare
                  </el-button>
                  <el-button type="text"
                             size="mini"
                             v-if="node.level === 3 ? true : false"
                             @click.stop="() => syncSingleFile(node, data)">
                    Sync
                  </el-button>
                </span>
              </span>
            </el-tree>
          </el-scrollbar>
        </el-col>
      </el-col>
      <el-col :span="17">
        <el-col :span="24"
                v-show="fileFlag">
          <el-divider content-position="left">{{textFileName}}</el-divider>
        </el-col>
        <el-table ref="fileResultTable"
                  :data="compareResult"
                  stripe
                  border
                  style="width: 100%"
                  size="mini"
                  :max-height="resultHeight"
                  highlight-current-row
                  v-show="fileFlag">
          <el-table-column v-for="col in regularColumns"
                           :prop="col.prop"
                           :key="col.name"
                           :label="col.label"
                           show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="name"
                           label="Name"
                           show-overflow-tooltip></el-table-column>
          <el-table-column prop="status"
                           label="Status"
                           width="80"
                           :filters="[{text: 'True', value: 0},{text: 'False', value: -1}]"
                           :filter-method="filterStatus">
            <template slot-scope="scope">
              <span v-if="scope.row.status == 0">True</span>
              <span v-else
                    style="color: red">False</span>
            </template>
          </el-table-column>
          <el-table-column prop="method"
                           label="Method"
                           width="80"
                           show-overflow-tooltip></el-table-column>
          <el-table-column prop="sourceValue"
                           label="sourceValue"
                           show-overflow-tooltip></el-table-column>
          <el-table-column prop="targetValue"
                           label="targetValue"
                           show-overflow-tooltip></el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-dialog :title="textFileName"
               :visible.sync="dialogTableVisible"
               width="70%">
      <el-table ref="textResultTable"
                :data="textCompareResult1"
                max-height="400"
                stripe
                border
                size="mini">
        <el-table-column type="index"
                         width="50">
        </el-table-column>
        <el-table-column property="tag"
                         label="status"
                         width="80"
                         :filters="[{text: 'INSERT', value: 'INSERT'},{text: 'DELETE', value: 'DELETE'},{text: 'CHANGE', value: 'CHANGE'},{text: 'EQUAL', value: 'EQUAL'}]"
                         :filter-method="filterHandler">
          <template slot-scope="scope">
            <span v-if="scope.row.tag == 'EQUAL'">EQUAL</span>
            <span v-else
                  style="color: red">{{ scope.row.tag }}</span>
          </template>
        </el-table-column>
        <el-table-column property="oldLine"
                         label="source">
          <template slot-scope="scope">
            <div v-html="scope.row.oldLine"></div>
          </template>
        </el-table-column>
        <el-table-column property="newLine"
                         label="target">
          <template slot-scope="scope">
            <div v-html="scope.row.newLine"></div>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      resultHeight: this.$store.state.clientHeight - 225,
      iniColumns: [
        { label: 'Section', prop: 'sectionName' }
      ],
      propertiesColumns: [
        { label: 'Key', prop: 'name' },
        { label: 'Status', prop: 'status', width: 80 },
        { label: 'Method', prop: 'method', width: 80 },
        { label: 'sourceValue', prop: 'sourceValue' },
        { label: 'targetValue', prop: 'targetValue' }
      ],
      textFileName: '',
      regularColumns: [],
      fileFlag: false,
      dirFlag: false,
      compareResult: [],
      treeData: [
      ],
      dialogTableVisible: false,
      textCompareResult: [
      ],
      textCompareResult1: [
      ],
      html: '',
      data: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      textDiffValue: 0,
      treeStyle: { height: this.$store.state.clientHeight - 215 + 'px' }
    }
  },
  created () {
    // this.loadProjectData()
    this.loadTreeData()
  },
  methods: {
    filterHandler (value, row, column) {
      console.log(value)
      console.log(row)
      console.log(column)
      const property = column['property']
      return row[property] === value
    },
    filterStatus (value, row, column) {
      console.log(value)
      console.log(row)
      console.log(column)
      const property = column['property']
      return row[property] === value
    },
    textDiffChange (val) {
      console.log(val)
      if (val === '1') {
        this.textCompareResult1 = this.textCompareResult.filter(item => {
          return item.tag !== 'EQUAL'
        })
        return
      }
      this.textCompareResult1 = this.textCompareResult
    },
    collectFile () {
      let checkNode = this.getCheckedNodes()
      var ids = []
      checkNode.forEach(function (item) {
        if (item.name) {
          ids.push(item.projectId)
        }
      })

      let projects = checkNode.filter(function (item) {
        return item.name
      })
      if (!projects || projects.length === 0) {
        this.$message({
          type: 'info',
          message: 'No data selected, please select a node first.'
        })
        return
      }
      // checkNode.forEach(function (item) {
      //   if (item.fileId) {
      //     that.$refs.tree.remove(item)
      //   }
      // })
      console.log(projects)
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.axios.post('/collect/project', projects).then((response) => {
        loading.close()
        this.$message({
          type: 'success',
          message: 'Collect file complete, show the file by click the host node.'
        })
        this.loadTreeData()
      }).catch((response) => {
        console.log(response)
      })
    },
    syncSingleFile (node, data) {
      console.log('sync single file')
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.axios.get(`/sync/file/${data.id}`).then((response) => {
        loading.close()
        console.log(response.data)
        var res = response.data || {}
        if (res.resultCode === 0) {
          this.$message({
            type: 'success',
            message: 'Sync file to target server success.'
          })
          return
        }
        this.$message({
          type: 'warning',
          message: res.errMsg
        })
      }).catch((response) => {
        loading.close()
        console.log(response)
      })
    },
    compareSingleFile (node, data) {
      console.log('compare single file')
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.axios.get(`/compare/execute/file/${data.id}`).then((response) => {
        loading.close()
        console.log(response.data)
        var res = response.data || {}
        if (res.resultCode === 0) {
          this.displayCompareResult(res, data.fileName)
          this.$message({
            type: 'success',
            message: 'Compare file complete.'
          })
          return
        }
        this.$message({
          type: 'warning',
          message: res.errMsg
        })
      }).catch((response) => {
        loading.close()
        console.log(response)
      })
    },
    syncFile () {
      let checkNode = this.getCheckedNodes()
      var ids = []
      checkNode.forEach(function (item) {
        if (item.name) {
          ids.push(item.projectId)
        }
      })
      let projects = checkNode.filter(function (item) {
        return item.name
      })
      if (!projects || projects.length === 0) {
        this.$message({
          type: 'info',
          message: 'Please select the file.'
        })
        return
      }
      console.log(projects)
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })

      this.axios.post(`/compare/sync`, projects).then((response) => {
        loading.close()
        this.$message({
          type: 'success',
          message: 'Sync file complete.'
        })
      }).catch((response) => {
        console.log(response)
        loading.close()
      })
    },
    syncFile1 () {
      let checkNode = this.getCheckedNodes()
      var ids = []
      checkNode.forEach(function (item) {
        if (item.fileId) {
          ids.push(item.id)
        }
      })
      if (!ids || ids.length === 0) {
        this.$message({
          type: 'info',
          message: 'No data selected, please select a node first.'
        })
        return
      }
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })

      this.axios.post(`/sync/files`, ids).then((response) => {
        loading.close()
        console.log(response.data)
        this.$message({
          type: 'success',
          message: 'Sync file complete.'
        })
      }).catch((response) => {
        console.log(response)
        loading.close()
      })
    },
    downloadReport () {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.axios.get(`/compare/report`).then((response) => {
        loading.close()
        if (response.data.resultCode === 0) {
          window.location = '/download/file/' + response.data.filePath
          return
        }
        this.$message({
          type: 'success',
          message: response.data.errMsg
        })
      }).catch((response) => {
        console.log(response)
        loading.close()
      })
    },
    compareFile () {
      let checkNode = this.getCheckedNodes()
      var ids = []
      checkNode.forEach(function (item) {
        if (item.fileId) {
          ids.push(item.id)
        }
      })
      if (!ids || ids.length === 0) {
        this.$message({
          type: 'info',
          message: 'No data selected, please select a node first.'
        })
        return
      }
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.axios.post('/compare/execute/file', ids).then((response) => {
        loading.close()
        this.$message({
          type: 'success',
          message: 'Compare file complete.Show the result by click the file node.'
        })
      }).catch((response) => {
        loading.close()
        this.$message({
          type: 'warn',
          message: 'Compare file Failed.Please refer to the log.'
        })
      })
      console.log(checkNode)
    },
    getCheckedNodes () {
      // (leafOnly, includeHalfChecked) 接收两个 boolean 类型的参数，1. 是否只是叶子节点，默认值为 false 2. 是否包含半选节点，默认值为 false
      var checkNode = this.$refs.tree.getCheckedNodes(false, false)
      return checkNode
    },
    getCheckedKeys () {
      console.log(this.$refs.tree.getCheckedKeys())
    },
    formatStatus (row, column, cellValue, index) {
      if (cellValue === 'EQUAL') {
        return '<span class="">' + cellValue + '</span>'
      } else {
        return '<span class="" style="color: red">' + cellValue + '</span>'
      }
    },
    loadProjectData () {
      this.axios.get('/project').then((response) => {
        // console.log(response.data)
        this.treeData = response.data.item || []
        if (this.treeData.length === 0) {
          return
        }
        this.treeData.forEach(element => {
          element.id = element.projectId
          element.leaf = false
          element.label = element.name
        })
      }).catch((response) => {
        console.log(response)
      })
    },
    loadTreeData () {
      this.axios.get('/collect/all').then((response) => {
        console.log(response.data)
        let res = response.data
        let files = res.file || []
        let hosts = res.host || []
        let projects = res.project || []
        projects.forEach((project) => {
          project.id = project.projectId
          project.label = project.name
          if (!project.children) {
            project.children = []
          }
          hosts.forEach((host) => {
            if (!host.children) {
              host.children = []
            }
            files.forEach((file) => {
              if (file.fileId === host.hostId) {
                file.label = file.fileName
                host.children.push(file)
              }
            })
            if (host.masterFlag === 0 && host.projectId === project.projectId) {
              host.id = host.hostId
              host.label = host.user + '@' + host.hostIp + ':' + host.port
              project.children.push(host)
            }
          })
        })
        this.treeData = projects || []
      }).catch((response) => {
        console.log(response)
      })
    },
    load (tree, treeNode, resolve) {
      if (tree.hostId && tree.projectId) {
        this.axios.get('/file/project/' + tree.projectId).then((response) => {
          let fileData = response.data.item || []
          if (fileData.length === 0) {
            // this.formData = []
            return
          }
          fileData.forEach((item) => {
            item.id = item.fileId
            let index = item.target.lastIndexOf('/')
            console.log(index)
            item.name = item.target.substring(index + 1)
          })
          resolve(fileData)
        }).catch((response) => {
          console.log(response)
        })
      } else if (tree.projectId) {
        this.axios.get('/host/project/' + tree.projectId).then((response) => {
          let serverData = response.data.item || []
          if (serverData.length === 0) {
            // this.formData = []
            return
          }
          serverData.forEach((item) => {
            item.id = item.hostId
            item.hasChildren = true
            item.name = item.user + '@' + item.hostIp + ':' + item.port
          })
          resolve(serverData)
        }).catch((response) => {
          console.log(response)
        })
      }

      // setTimeout(() => {
      //   resolve([
      //     {
      //     }
      //   ])
      // }, 1000)
    },
    displayCompareResult (result, fileName) {
      if (result.resultCode === -1) {
        this.$message({
          type: 'warning',
          message: result.errMsg
        })
        return
      }
      this.textFileName = fileName
      if (result.type === 0) {
        let temp = result.result || []
        this.textDiffValue = 0
        this.dialogTableVisible = true
        this.textCompareResult = temp
        this.textCompareResult1 = temp
        this.$refs.textResultTable.clearFilter()
      } else if (result.type === 1) {
        this.regularColumns = []
        this.compareResult = result.result
        this.fileFlag = true
        this.$refs.fileResultTable.clearFilter()
      } else if (result.type === 2) {
        this.regularColumns = this.iniColumns
        this.compareResult = result.result
        this.fileFlag = true
        this.regularFlag = true
        this.$refs.fileResultTable.clearFilter()
      }
    },
    handleNodeClick (data) {
      console.log(data)
      this.fileFlag = false
      this.dirFlag = false
      if (!data.fileId) {
        return
      }
      this.axios.get(`/compare/result1/${data.id}`).then((response) => {
        console.log(response.data)
        let result = response.data
        if (!result) {
          this.$message({
            type: 'info',
            message: 'No data display, please compare the file first.'
          })
        }
        this.displayCompareResult(result, data.fileName)
      }).catch((response) => {
        console.log(response)
      })
    },
    loadNode (node, resolve) {
      console.log(node)
      let projectId = node.data.projectId || ''
      if (!projectId) {
        return
      }
      if (node.level === 1) {
        this.axios.get(`/host/project/${projectId}`).then((response) => {
          let serverData = response.data.item || []
          if (serverData.length === 0) {
            return
          }
          console.log(serverData)
          serverData = serverData.filter((item) => {
            item.id = item.hostId
            item.projectId = projectId
            item.label = item.user + '@' + item.hostIp + ':' + item.port
            item.leaf = false
            return !item.masterFlag
          })
          console.log(serverData)
          resolve(serverData)
        }).catch((response) => {
          console.log(response)
        })
      } else if (node.level === 2) {
        let hostId = node.data.hostId
        this.axios.get(`/collect/${projectId}/${hostId}`).then((response) => {
          console.log(response)
          let fileData = response.data || []
          if (fileData.length === 0) {
            // this.formData = []
            return
          }
          fileData.forEach((item) => {
            // item.id = item.fileId
            item.hasChildren = false
            item.label = item.fileName
          })
          resolve(fileData)
        }).catch((response) => {
          console.log(response)
        })
      } else {
        resolve([])
      }
    }
  }
}
</script>
<style scope>
.el-divider__text {
  position: absolute;
  background-color: #f0f2f5;
  padding: 0 20px;
  color: #303133;
}
.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  padding-right: 8px;
}
.center {
  height: 60vh;
  overflow: auto;
}
.txt-table {
  border: 1px solid #777;
}
.txt-table tr {
  border: 1px solid #777;
}

.txt-table td {
  text-align: left;
}
.el-tree > .el-tree-node {
  min-width: 100%;
  display: inline-block;
}
</style>



