<template>
  <div>
    <el-aside id="asideNav">
      <div class="logo-name">
        <p v-if="$store.getters.logoShow">DR</p>
        <p v-else>Disaster Recovery</p>
      </div>
      <el-menu default-active="/index"
               class="el-menu-vertical"
               :collapse="$store.getters.isCollapse"
               background-color="#03152A"
               text-color="rgba(255,255,255,0.7)"
               active-text-color="#ffffff"
               :router="$store.getters.uniquerouter"
               :collapse-transition="true">
        <el-menu-item index="/index">
          <i class="el-icon-s-home"></i>
          <span slot="title">{{ $t(`menu.home`) }}</span>
        </el-menu-item>
        <el-menu-item index="/markdown"
                      v-show="false">
          <i class="el-icon-s-home"></i>
          <span slot="title">markdown</span>
        </el-menu-item>
        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-setting"></i>
            <span>{{ $t(`menu.baseConfig`) }}</span>
          </template>
          <el-menu-item index="/project">
            <i class="el-icon-menu"></i>
            {{ $t(`menu.projectConfig`) }}
          </el-menu-item>
          <el-menu-item index="/server">
            <i class="el-icon-monitor"></i>
            {{ $t(`menu.hostConfig`) }}
          </el-menu-item>
          <el-menu-item index="/file">
            <i class="el-icon-document"></i>
            {{ $t(`menu.fileConfig`) }}
          </el-menu-item>
        </el-submenu>
        <el-menu-item index="/compare">
          <i class="el-icon-menu"></i>
          <span slot="title">{{ $t(`menu.fileCompare`) }}</span>
        </el-menu-item>
        <el-menu-item index="/test"
                      v-show="false">
          <i class="el-icon-document"></i>
          test
        </el-menu-item>
      </el-menu>
    </el-aside>
  </div>
</template>

<script>
export default {
  name: 'asideNav',
  data () {
    return {
      isCollapse: false
    }
  },
  components: {
  },
  watch: {
    // 监听浏览器直接输入路由，将此路由添加到tabnavBox
    '$route.path': function (val) {
      this.selectmenu(val)
    }
  },
  methods: {
    selectmenu (key) {
      this.$store.dispatch('addTab', {
        title: key,
        path: key
      })
    }
  }
}
</script>
<style lang="scss" scoped>
$top: top;
$bottom: bottom;
$left: left;
$right: right;
%w100 {
  width: 100%;
}

%h100 {
  height: 100%;
}

%cursor {
  cursor: pointer;
}

@mixin set-value($side, $value) {
  @each $prop in $leftright {
    #{$side}-#{$prop}: $value;
  }
}

#asideNav {
  width: auto !important;
  display: flex;
  flex-direction: column;
  border-right: solid 1px #e6e6e6;
  .logo-name {
    background-color: #03152a !important;
    @extend %w100;
    font-weight: 300;
    z-index: 999;
    p {
      height: 50px;
      line-height: 50px;
      text-align: center;
      font-size: 16px;
      color: #5e6d82;
    }
  }
  .el-menu-vertical:not(.el-menu--collapse) {
    width: 200px;
    @extend %h100;
    /*overflow-y: scroll;*/
  }
  .el-menu {
    flex: 1;
    overflow: inherit;
    border-right: none;
    text-align: left;
    &::-webkit-scrollbar {
      display: none;
    }
    .fa {
      vertical-align: middle;
      margin-right: 5px;
      width: 24px;
      text-align: center;
      font-size: 18px;
    }
    .el-menu-item {
      background-color: #020f1d !important;
      border-bottom: 1px solid #020f1d;
      &:hover {
        color: #ffffff !important;
        background-color: #375573 !important;
      }
    }
    .el-menu-item.is-active {
      background-color: #56a9ff !important;
    }
    .is-opened > .el-submenu__title > .el-icon-arrow-down {
      color: #ffffff;
      font-weight: 500;
      font-size: 18px;
    }
  }
}
</style>



