<template>
  <el-row :gutter="15">
    <el-col :span="24"
            style="padding-bottom:0">
      <el-divider content-position="left">File Config</el-divider>
    </el-col>
    <el-col :span="12"
            style="height: 50px">
      <projectSelect @projectChange="projectChange"></projectSelect>
    </el-col>
    <el-col :span="24">
      <el-table ref="fileTable"
                :data="fileData"
                style="width: 100%"
                max-height="400"
                size="mini"
                highlight-current-row
                @current-change="handleCurrentChange">
        <el-table-column prop="fileName"
                         label="File Name"
                         show-overflow-tooltip></el-table-column>
        <el-table-column prop="type"
                         label="File Type"
                         :formatter="formatFileType"></el-table-column>
        <el-table-column prop="generalPath"
                         label="Default Path"
                         show-overflow-tooltip></el-table-column>
        <el-table-column prop="comments"
                         label="Comments"></el-table-column>
        <el-table-column label="Action"
                         width="120">
          <template slot-scope="scope">
            <el-button @click.native.prevent="deleteRow(scope.$index, scope.row)"
                       type="text"
                       size="small">{{ $t("delete")}}</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
    <el-col :span="24">
      <el-divider></el-divider>
    </el-col>
    <el-col :span="24">
      <el-collapse v-model="activeNames"
                   @change="handleChange">
        <el-collapse-item class="collapse-title"
                          name="1">
          <template slot="title">Detail</template>
          <el-col :span="7">
            <el-col :span="24">
              <el-form ref="fileForm"
                       :model="formData"
                       label-width="auto"
                       :disabled="!editFlag"
                       size="mini"
                       :rules="rules">
                <el-col :span="24"
                        style="display:none;">
                  <el-form-item label="Project Id"
                                prop="projectId">
                    <el-input v-model="formData.projectId"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="24"
                        style="display:none;">
                  <el-form-item label="File Id"
                                prop="fileId">
                    <el-input v-model="formData.fileId"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="24">
                  <el-form-item label="File Name"
                                prop="fileName"
                                required>
                    <el-input v-model="formData.fileName"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="24">
                  <el-form-item label="File Type"
                                prop="type"
                                required>
                    <el-select v-model="formData.type"
                               placeholder
                               style="width:100%">
                      <el-option v-for="item in typeData"
                                 :key="item.value"
                                 :label="item.label"
                                 :value="item.value">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="24">
                  <el-form-item label="Default Path"
                                prop="generalPath"
                                required>
                    <el-input v-model="formData.generalPath"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="24">
                  <el-form-item label="Comments"
                                prop="comments">
                    <el-input v-model="formData.comments"></el-input>
                  </el-form-item>
                </el-col>
              </el-form>
            </el-col>
          </el-col>
          <el-col :span="8">
            <el-col :span="24">
              <el-table ref="deltaFileInfo"
                        :data="formData.deltaFileInfos"
                        style="width: 100%"
                        size="mini"
                        max-height="250"
                        highlight-current-row
                        border>
                <el-table-column prop="hostId"
                                 label="Host"
                                 show-overflow-tooltip>
                  <template slot-scope="{row}">
                    <span v-if="editFlag">
                      <el-select v-model="row.hostId"
                                 @change="hostChange()"
                                 placeholder=""
                                 size="mini">
                        <el-option v-for="item in serverData"
                                   :key="item.hostId"
                                   :label="item.label"
                                   :value="item.hostId"></el-option>
                      </el-select>
                    </span>
                    <span v-else>{{row.hostInfo.user}}@{{ row.hostInfo.hostIp }}:{{row.hostInfo.port}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="path"
                                 label="Path"
                                 show-overflow-tooltip>
                  <template slot-scope="{row}">
                    <span v-if="editFlag">
                      <el-input size="mini"
                                placeholder=""
                                v-model="row.path">
                      </el-input>
                    </span>
                    <span v-else>{{ row.path }}</span>
                  </template>
                </el-table-column>
                <el-table-column label="Action"
                                 width="60"
                                 align="center">
                  <template slot-scope="scope">
                    <el-button @click.native.prevent="deleteDeltaFileInfoRow(scope.$index, formData.deltaFileInfos)"
                               :disabled="!editFlag"
                               type="text"
                               size="small">{{ $t("delete") }}</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-col>
            <el-col :span="24"
                    style="text-align: left; padding-top: 10px">
              <el-button type="primary"
                         @click="onAddDeltaFileInfo"
                         title="Add Delta file info"
                         :disabled="!editFlag"
                         size="mini">Add</el-button>
            </el-col>
          </el-col>
          <el-col :span="9">
            <el-col :span="24">
              <el-table ref="valueTable"
                        :data="formData.specialValues"
                        style="width: 100%"
                        size="mini"
                        max-height="250"
                        highlight-current-row
                        border>
                <el-table-column prop="sectionName"
                                 label="section"
                                 show-overflow-tooltip>
                  <template slot-scope="{row}">
                    <span v-if="editFlag">
                      <el-input size="mini"
                                placeholder=""
                                v-model="row.sectionName">
                      </el-input>
                    </span>
                    <span v-else>{{ row.sectionName }}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="key"
                                 label="Key"
                                 show-overflow-tooltip>
                  <template slot-scope="{row}">
                    <span v-if="editFlag">
                      <el-input size="mini"
                                placeholder=""
                                v-model="row.key">
                      </el-input>
                    </span>
                    <span v-else>{{ row.key }}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="method"
                                 label="Method"
                                 width="80"
                                 show-overflow-tooltip>
                  <template slot-scope="{row}">
                    <span v-if="editFlag">
                      <el-select v-model="row.method"
                                 placeholder
                                 style="width:100%"
                                 size="mini">
                        <el-option v-for="item in compareTypeData"
                                   :key="item.value"
                                   :label="item.label"
                                   :value="item.value"></el-option>
                      </el-select>
                    </span>
                    <span v-else>{{ row.typeInfo.label }}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="value"
                                 label="Value"
                                 show-overflow-tooltip>
                  <template slot-scope="{row}">
                    <span v-if="editFlag">
                      <el-select v-model="row.value"
                                 clearable
                                 placeholder=""
                                 size="mini">
                        <el-option v-for="item in hostData"
                                   :key="item.key"
                                   :label="item.label"
                                   :value="item.key"></el-option>
                      </el-select>
                    </span>
                    <span v-else>{{ row.value }}</span>
                  </template>
                </el-table-column>
                <el-table-column label="Action"
                                 width="60"
                                 align="center">
                  <template slot-scope="scope">
                    <el-button @click.native.prevent="deleteValueRow(scope.$index, formData.specialValues)"
                               :disabled="!editFlag"
                               type="text"
                               size="small">{{ $t("delete") }}</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-col>
            <el-col :span="24"
                    style="text-align: left; padding-top: 10px">
              <el-button type="primary"
                         @click="onAddAdditionValue"
                         :disabled="!editFlag"
                         size="mini">Add</el-button>
            </el-col>
          </el-col>
          <el-col :span="24"
                  v-show="!editFlag"
                  class="js-btn-group">
            <el-button type="primary"
                       @click="onAdd"
                       :disabled="!addPermit"
                       size="mini">{{ $t(`new`) }}</el-button>
            <el-button type="primary"
                       @click="onEdit"
                       :disabled="!editPermit"
                       size="mini">{{ $t(`edit`) }}</el-button>
            <!-- <el-button size="mini">{{ $t(`delete`) }}</el-button> -->
          </el-col>
          <el-col :span="24"
                  v-show="editFlag"
                  class="js-btn-group">
            <el-button type="primary"
                       @click="onSubmit"
                       size="mini">{{ $t(`el.colorpicker.confirm`) }}</el-button>
            <el-button @click="onCancel"
                       size="mini">{{ $t(`el.messagebox.cancel`) }}</el-button>
          </el-col>
        </el-collapse-item>
      </el-collapse>
    </el-col>
  </el-row>
</template>

<script>
import projectSelect from './projectSelect'
export default {
  name: 'fileTable',
  components: { projectSelect },
  created () { },
  data () {
    return {
      compareTypeData: [
        { label: 'ignore', value: 1 },
        { label: 'existed', value: 2 },
        { label: 'notExisted', value: 3 },
        { label: 'specific value', value: 4 }
      ],
      typeMap: {
        1: { label: 'ignore', value: 1 },
        2: { label: 'existed', value: 2 },
        3: { label: 'notExisted', value: 3 },
        4: { label: 'specific value', value: 4 }
      },
      // encodingData: [
      //   { label: 'UTF-8', value: 'UTF-8' },
      //   { label: 'US-ASCII', value: 'US-ASCII' },
      //   { label: 'GBK', value: 'GBK' },
      //   { label: 'ISO-8859-1', value: 'ISO-8859-1' },
      //   { label: 'Unicode', value: 'Unicode' }
      // ],
      typeData: [
        { label: 'Others', value: 0 },
        { label: 'Properties', value: 1 },
        { label: 'Ini', value: 2 }
      ],
      serverData: [],
      hostInfoMap: {},
      valueFlag: false,
      addPermit: true,
      editPermit: true,
      editFlag: false,
      action: 'view',
      activeNames: ['1'],
      projectData: [],
      fileData: [],
      currentRow: {},
      formData: {
        specialValues: [],
        deltaFileInfos: []
      },
      projectId: '',
      rules: {},
      hostData: []
    }
  },
  methods: {
    loadServerData (id) {
      this.axios.get(`/host/project/${id}`).then((response) => {
        let serverData = response.data.item || []
        console.log(serverData)
        this.serverData = serverData.map(item => {
          item.label = item.user + '@' + item.hostIp + ':' + item.port
          return item
        })
        console.log(this.serverData)
        this.hostInfoMap = this.tranHostList2Map(serverData)
      }).catch((response) => {
        console.log(response)
      })
    },
    projectChange (val) {
      this.projectId = val
      this.loadServerData(val)
      this.loadFileData(val)
      this.axios
        .get(`/host/4file/${val}`)
        .then(response => {
          console.log(response.data)
          var hostData = response.data || []
          if (hostData.length === 0) {
            this.$message({
              type: 'warning',
              message: 'The server is not configured for the current project, please add server first.'
            })
            this.hostData = []
            // this.hostMap = {}
            return
          }
          hostData.map((value, index, array) => {
            value.label = this.camel2SingleWord(value.key)
          })
          console.log(hostData)
          this.hostData = hostData
          // this.hostMap =
        })
        .catch(response => {
          console.log(response)
        })
    },
    loadFileData (id) {
      this.editFlag = false
      this.axios
        .get('/file/project/' + id)
        .then(response => {
          this.fileData = response.data.item || []
          if (this.fileData.length === 0) {
            this.formData = {
              specialValues: [],
              deltaFileInfos: []
            }
            this.$refs.fileForm.resetFields()
            this.editPermit = false
            return
          }
          this.$refs.fileTable.setCurrentRow(this.fileData[0])
        })
        .catch(response => {
          console.log(response)
        })
    },
    onAddAdditionValue () {
      if (!this.formData.specialValues) {
        this.formData.specialValues = []
      }
      this.formData.specialValues.push({})
      console.log('add addtion value')
    },
    onAddDeltaFileInfo () {
      if (!this.formData.deltaFileInfos) {
        this.formData.deltaFileInfos = []
      }
      this.formData.deltaFileInfos.push({})
      console.log('add addtion value')
    },
    formatFileType (row, column, cellValue, index) {
      switch (cellValue) {
        case 1:
          return 'properties'
        case 2:
          return 'ini'
        default:
          return 'others'
      }
    },
    deleteRow (index, row) {
      this.$confirm('Are you sure to delete ?', 'Confirm', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        this.axios.delete(`/file/${row.fileId}`).then((response) => {
          this.$message({
            type: 'success',
            message: 'Delete file success!'
          })
          this.fileData.splice(index, 1)
        }).catch((response) => {
          console.log(response)
        })
      }).catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: 'Delete canceled.'
        // })
      })
    },
    deleteValueRow (index, rows) {
      rows.splice(index, 1)
    },
    deleteDeltaFileInfoRow (index, rows) {
      rows.splice(index, 1)
    },
    showAdditionValue (index, rows) {
      // this.valueFlag = true
    },
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },
    handleChange (val) {
      console.log(val)
    },
    handleCurrentChange (val) {
      this.currentRow = val
      this.editPermit = true
      this.formData = JSON.parse(JSON.stringify(val))
      this.editFlag = false
      // todo 需要过滤掉已经删除了的host
      var deltaFileInfos = this.formData.deltaFileInfos || []
      if (deltaFileInfos.length > 0) {
        this.formData.deltaFileInfos = deltaFileInfos.filter(item => {
          item.hostInfo = this.hostInfoMap[item.hostId]
          return this.hostInfoMap[item.hostId]
        })
      }

      var specialValues = this.formData.specialValues || []
      console.log(specialValues)
      if (specialValues.length > 0) {
        specialValues.forEach(element => {
          console.log(element)
          console.log(this.typeMap)
          element.typeInfo = this.typeMap[element.method]
        })
        console.log(specialValues)
        this.formData.specialValues = specialValues
      }
      this.$refs.fileForm.resetFields()
    },
    tranHostList2Map (hostData) {
      var result = {}
      hostData.forEach(element => {
        result[element.hostId] = element
      })
      return result
    },
    onAdd () {
      this.editFlag = true
      this.action = 'new'
      this.$refs.fileForm.resetFields()
      this.formData = {
        fileId: '',
        projectId: this.projectId,
        specialValues: [],
        deltaFileInfos: []
      }
    },
    onEdit () {
      this.editFlag = true
      this.action = 'edit'
    },
    validateForm (data) {
      var deltaFileInfos = data.deltaFileInfos || []
      var specialValues = data.specialValues || []
      if (deltaFileInfos.length === 0 && specialValues.length === 0) {
        return true
      }
      var that = this
      var flag = true
      var deltaFlag = deltaFileInfos.some(function (x) {
        return !x.hostId || !x.path
      })
      if (deltaFlag) {
        that.$message({
          message: 'Please check the addition file info value, the hostId and path is required.',
          type: 'warning'
        })
        return false
      }
      if (!flag) {
        return false
      }
      var type = data.type
      if (type !== 1 && type !== 2) {
        return true
      }
      var specialValueFlag = specialValues.some(function (x) {
        console.log(x)
        if (x.method === 4 && !x.value) {
          that.$message({
            message: 'When method is special value, The special value is not configured.',
            type: 'warning'
          })
          return true
        }
        if (type === 1) {
          if (!x.key || !x.method) {
            that.$message({
              message: 'The [key, method] is required.',
              type: 'warning'
            })
            return true
          }
          return false
        } else if (type === 2) {
          if (!x.sectionName || !x.key || !x.method) {
            that.$message({
              message: 'The [section, key, method] is required.',
              type: 'warning'
            })
            return true
          }
          return false
        }
        return false
      })
      return !specialValueFlag
    },
    onSubmit () {
      if (!this.validateForm(this.formData)) {
        return
      }
      this.$refs['fileForm'].validate((valid) => {
        if (!valid) {
          console.log('error submit!!')
          return false
        }
        if (this.action === 'new') {
          this.axios
            .post(`/file`, this.formData)
            .then(response => {
              if (response.data.resultCode === 0) {
                this.$message({
                  message: 'Add FileInfo success.',
                  type: 'success'
                })
                this.loadFileData(this.projectId)
                // this.fileData.push(response.data.fileInfo)
                this.$refs.btnGroup.setEditFlag(false)
                return
              }
              this.$message({
                message: response.data.errMsg,
                type: 'warning'
              })
            })
            .catch(response => {
              console.log(response)
            })
        } else if (this.action === 'edit') {
          this.axios
            .put(`/file`, this.formData)
            .then(response => {
              if (response.data.resultCode === 0) {
                this.$message({
                  message: 'Modify fileInfo success.',
                  type: 'success'
                })
                this.loadFileData(this.projectId)
                return
              }
              this.$message({
                message: response.data.errMsg,
                type: 'warning'
              })
            })
            .catch(response => {
              console.log(response)
            })
        }
      })
    },
    onCancel () {
      this.editFlag = false
      this.formData = JSON.parse(JSON.stringify(this.currentRow))
      var deltaFileInfos = this.formData.deltaFileInfos || []
      if (deltaFileInfos.length > 0) {
        this.formData.deltaFileInfos = deltaFileInfos.filter(item => {
          item.hostInfo = this.hostInfoMap[item.hostId]
          return this.hostInfoMap[item.hostId]
        })
      }

      var specialValues = this.formData.specialValues || []
      console.log(specialValues)
      if (specialValues.length > 0) {
        specialValues.forEach(element => {
          console.log(element)
          console.log(this.typeMap)
          element.typeInfo = this.typeMap[element.method]
        })
        console.log(specialValues)
        this.formData.specialValues = specialValues
      }
      this.$refs.fileForm.resetFields()
    },
    camel2SingleWord (str) {
      return str.replace(/([A-Z])/g, ' $1').toLowerCase()
    }
  }
}
</script>

<style scoped>
.collapse-title .el-collapse-item__header {
  padding-left: 10px;
  color: cornflowerblue;
  font-weight: 700;
}
.el-divider--horizontal {
  margin: 12px 0;
}
h3 {
  margin: 25px 0 20px;
  font-weight: 400;
  color: #1f2f3d;
  font-size: 22px;
}
p {
  font-size: 14px;
  color: #5e6d82;
  line-height: 1.5em;
}
.el-divider__text {
  position: absolute;
  background-color: #f0f2f5;
  padding: 0 20px;
  color: #303133;
}
.js-btn-group {
  text-align: right;
  padding-bottom: 15px;
}
</style>



