<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="8">
        <div class="card kjfs"
             style="display:none">
          <p class="title">
            <i class="fa fa-th-large fa-lg"></i>快捷方式
          </p>
          <ul>
            <li>
              <router-link to="/addArticle"
                           class="kjfs kjfs-bluee">
                <span>
                  <i class="el-icon-tickets fa-2x"></i>
                </span>
                <span>发表文章</span>
              </router-link>
            </li>
            <li>
              <router-link to="/dragTabe"
                           class="kjfs kjfs-pinkk">
                <span>
                  <i class="fa fa-hand-stop-o fa-2x"></i>
                </span>
                <span>拖拽排序</span>
              </router-link>
            </li>
            <li>
              <router-link to="/pagePermissions"
                           class="kjfs kjfs-yelloww">
                <span>
                  <i class="fa fa-universal-access fa-2x"></i>
                </span>
                <span>权限测试</span>
              </router-link>
            </li>
          </ul>
          <ul>
            <li>
              <router-link to="/upload"
                           class="kjfs kjfs-grennn">
                <span>
                  <i class="fa fa-cloud-upload fa-2x"></i>
                </span>
                <span>文件上传</span>
              </router-link>
            </li>
            <li>
              <router-link to="/navClassifies"
                           class="kjfs kjfs-purplee">
                <span>
                  <i class="fa el-icon-menu fa-2x"></i>
                </span>
                <span>前台菜单</span>
              </router-link>
            </li>
            <li>
              <router-link to="/markdown"
                           class="kjfs kjfs-lightBluee">
                <span>
                  <i class="fa fa-file-code-o fa-2x"></i>
                </span>
                <span>编辑器</span>
              </router-link>
            </li>
          </ul>
        </div>
      </el-col>
      <el-col :span="8"
              style="display:none">
        <div class="card dbsx">
          <p class="title">
            <i class="fa fa-file-text-o"></i>待办事项
          </p>
          <ul>
            <li>
              <router-link to="#">
                <span>待审评论</span>
                <span class="num">66</span>
              </router-link>
            </li>
            <li>
              <router-link to="#">
                <span>待审帖子</span>
                <span class="num">66</span>
              </router-link>
            </li>
          </ul>
          <ul>
            <li>
              <router-link to="#">
                <span>待审商品</span>
                <span class="num">66</span>
              </router-link>
            </li>
            <li>
              <router-link to="#">
                <span>代发货</span>
                <span class="num">66</span>
              </router-link>
            </li>
          </ul>
        </div>
      </el-col>
      <el-col :span="24"
              v-show="false">
        <div class="card bbxx">
          <p class="title">
            <i class="fa fa-server"></i>版本信息
          </p>
          <div class="table">
            <p>
              <span class="tit">当前版本</span>v1.0.0
            </p>
            <p>
              <span class="tit">基于框架</span>vue2.0全家桶 + element-ui + Springboot
            </p>
          </div>
        </div>
      </el-col>
      <el-col :span="24">
        <div class="bbxx"
             style="text-align: left">
          <h3>页面操作</h3>
          <p>分为四个页面，project config（项目配置）,server config(服务器配置)，file config（文件配置），file compare（文件对比同步）</p>
          <br />
          <h4>project config 为项目配置页面，可以对项目进行增删改查操作</h4>
          <br />
          <h4>server config 为服务器配置页面</h4>
          <p>
            一个项目可以对应多个服务器，首先上部下拉框选择项目，然后进行服务器配置的相关操作，可以分别配置基本属性（地址，端口，用户，密码，是否为基准主机），
            <br />服务器ip需要满足对应ip地址规则，端口范围1-65535，修改或者新增服务器配置提交，后台会对尝试以当前配置的ip端口，用户名密码登录对应服务器，
            <br />如果登录失败，前台弹出提示，需要配置正确能成功登录服务器方允许提交，新增服务器信息自定义配置，每个项目需要指定一台机器作为主机，其他机器
            <br />同一个项目下，一个ip地址只允许添加一次
            <br />以此机器上的文件作为基准进行对比，一个项目只允许指定一台主机
            <br />表单右边的表格可以配置服务器的附加属性，比如databaseIp，127.0.0.1,则配置key为databaseIp，值为127.0.0.1，此属性的key不能跟基本属性冲突
            <br />同一个key只能配置一次
          </p>
          <p>
            <br />
          </p>
          <h4>file config 为文件配置，即给服务器添加需要对比或者同步的文件；参数解释：</h4>
          <ul>
            <li>is directory：是否为文件夹；</li>
            <li>default path：文件所在目标服务器的默认路径，中间表格可以对单独的服务器配置路径，未配置的服务器则按照此默认路径查找文件</li>
            <li>File type 文件类型，仅在文件下有效，三种类型，properties键值对，ini文件，其他类型（作为普通文本）</li>
            <li>中间表格为附加文件信息，用于在服务器文件路径跟默认路径不配置情况下，单独配置路径，选定服务器，然后输入当前对比文件的路径即可
            <li>右边表格配置ini和properties文件具体属性的对比规则，配置文件跟当前主机相关，比如配置了当前主机的端口，ip等参数，(在右下表格Replace valueMap进行配置)</li>
            <li>对于properties键值对文件，需要配置key，mehod，value三个字段，key为键，
            <li>mehod为对比规则（1.ignore忽略改属性，2.existed目标主机该属性存在即可,3.notExisted 目标主机该属性不应存在4.指定值，该属性为指定值，需要同时配置value属性，用于指定服务器的某个属性值）；</li>
            <li>对于ini文件，相比properties文件，需要多配置一个section的值，其余同properties文件配置。</li>
          </ul>
          <br />
          <h4>file commpare 文件收集，对比，同步页面。左侧窗口分为左右两栏，左边为操作区域，右边为对比结果展示区域。</h4>
          <ul>
            <li>1.首先选择项目，下边自动展示项目下的服务器列表，</li>
            <li>2.勾选指定服务器，点击collect按钮，完成对文件的收集工作，收集的文件自动添加在服务器所在节点，</li>
            <li> (文件收集基于项目，选择项目后才能收集文件到本地；此操作由于网络通信，文件IO，收集文件可能需要耗费较多时间)</li>
            <li>3.勾选需要对比的文件，点击compare按钮，即可完成文件的对比，(文件对比基于文件，选取对应文件进行对比，选取项目或者服务器则默认选取子文件节点进行对比)</li>
            <li>4.同时也可点击compare进行单个文件对比，</li>
            <li>5.点击对应的文件节点即可在右侧展示对应的对比结果，不一致的地方会有红色标记，</li>
            <li>6.勾选对应的文件节点，点击Sync按钮可以完成同步文件到目标服务器的操作，此步操作会同时完成valuemap中对应字段的值的替换，同时对服务器目标文件进行备份（对于单个文件，在文件所在文件夹内生成'文件名_当前时间字符串.bak'文件；对于文件夹，则备份整个文件夹，tar打包为'文件夹名_当前时间字符串.tar.gz'）, 再把本地文件推送过去。</li>
            <li> (文件同步操作同样需要网络通信，IO操作，可能耗时较长)
            <li>7.文件推送同样基于文件，也可点击文件节点后的sync按钮对单个文件进行同步，</li>
            <li>8.推送完成后，可以再进行一次文件收集，对比，检查配置文件是否已经同步完成。</li>
            <li>9.报表下载，点击report按钮即可获取对比结果的报表，生成csv文件（获取当前已经对比过的文件的对比结果）</li>
          </ul>
          <br />
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'mainIndex',
  components: {},
  mounted () {
    this.selfAdaption()
  },
  methods: {
    // echart自适应
    selfAdaption () {
      let that = this
      setTimeout(() => {
        window.onresize = function () {
          if (that.$refs.echarts) {
            that.$refs.echarts.chart.resize()
          }
        }
      }, 10)
    }
  }
}
</script>

<style lang="scss" scoped>
$top: top;
$bottom: bottom;
$left: left;
$right: right;
$leftright: ($left, $right);
$pinkk: #eec2d3;
$bluee: #409eff;
$yelloww: #f4d884;
$grennn: #89dda0;
$purplee: #78a2ea;
$lightBluee: #b8d6ff;

$list: bluee pinkk yelloww grennn purplee lightBluee;
$list1: $bluee $pinkk $yelloww $grennn $purplee $lightBluee;
%shadow {
  background: #fff;
  -webkit-box-shadow: 4px 4px 40px rgba(0, 0, 0, 0.2);
  box-shadow: 4px 4px 40px rgba(0, 0, 0, 0.2);
  border-color: rgba(0, 0, 0, 0.2);
  .title {
    font-size: 14px;
    padding: 10px;
    i {
      margin-#{$right}: 5px;
    }
  }
}

@mixin flex($direction: row, $content: space-between) {
  display: flex;
  flex-direction: $direction;
  justify-content: $content;
}
.card {
  color: #666;
  @extend %shadow;

  ul {
    @include flex;
    li {
      flex: 1;
      a {
        color: #666666;
        align-items: center;
        padding-#{$top}: 20px;
        padding-#{$bottom}: 20px;
        @include flex(column);
        span {
          height: 44px;
        }
        .num {
          line-height: 44px;
          font-size: 42px;
          color: $bluee;
          margin: 0px;
        }
      }
      .kjfs-bluee {
        color: $bluee;
      }
      .kjfs-pinkk {
        color: $pinkk;
      }
      .kjfs-yelloww {
        color: $yelloww;
      }
      .kjfs-grennn {
        color: $grennn;
      }
      .kjfs-purplee {
        color: $purplee;
      }
      .kjfs-lightBluee {
        color: $lightBluee;
      }
      &:hover {
        .kjfs-bluee {
          color: #ffffff;
          background: $bluee;
        }
        .kjfs-pinkk {
          color: #ffffff;
          background: $pinkk;
        }
        .kjfs-yelloww {
          color: #ffffff;
          background: $yelloww;
        }
        .kjfs-grennn {
          color: #ffffff;
          background: $grennn;
        }
        .kjfs-purplee {
          color: #ffffff;
          background: $purplee;
        }
        .kjfs-lightBluee {
          color: #ffffff;
          background: $lightBluee;
        }
      }
    }
  }
  .table {
    padding: 21px;
    p {
      height: 52px;
      line-height: 52px;
      border: 1px solid #cccccc;
      overflow: hidden;
      border-#{$top}: none;
      @include flex(null, start);
      &:first-child {
        border-#{$top}: 1px solid #cccccc;
      }
      span {
      }
      .tit {
        width: 90px;
        min-width: 90px;
        height: 100%;
        text-align: center;
        border-#{$right}: 1px solid #cccccc;
        margin-#{$right}: 18px;
      }
      span.gitbox {
        flex: 1;
        height: 100%;
        overflow: hidden;
        @include flex(row, start);
        a {
          &:first-child {
            margin-#{$right}: 30px;
          }
        }
      }
    }
  }
}
#lineEcharts {
  margin-#{$top}: 30px;
  padding-#{$top}: 30px;
  @extend %shadow;
}
#maintable {
  margin-#{$top}: 30px;
  padding-#{$top}: 10px;
  @extend %shadow;
}
</style>



